@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content:center;
  background: url(/Tic-tac-toe/static/media/pic.ea3b51cf.jpg);
}
body{
  font-family: 'Roboto Condensed', sans-serif;
  font-weight: bold;
  color: #101010;
}
.box{
    width:150px;
    height:50px;
    display:flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
    box-shadow: 0 0 4px 0 grey;
    margin-left: 5%;
  background-color: rgba(255, 255, 255, 0.2);
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
  border-radius: 1rem;
  box-shadow:0 0 3px 0 grey ;
  border: none;
 

}
span{
  text-decoration: none;
  font-size: 2rem;
  color:whitesmoke;
  /* padding: 1rem; */
  position: relative;
  -webkit-perspective: 30rem;
          perspective: 30rem;
  -webkit-animation:rotate 7s infinite;
          animation:rotate 7s infinite;
  transition: all 2s;

}

.span::after{
  content:"";
  display:block;
  width:100%;
  height:100%;
  top:4.2rem;
  left:0;
  position:absolute;
  background-color: rgba(255, 255, 255, 0.2);
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
  border-radius: 1rem;
  -webkit-transform:rotateX(80deg);
          transform:rotateX(80deg);
  z-index: 2;
}
.span::before{
  content:"";
  display:block;
  width:100%;
  height:100%;
  top:0;
  left:0;
  position:absolute;
  background-color: rgba(255, 255, 255, 0.2);
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
  border-radius: 1rem;
  -webkit-transform:rotateX(80deg);
          transform:rotateX(80deg);
  z-index: -1;

}

@-webkit-keyframes rotate {
      0% {-webkit-transform: rotateY(180deg);transform: rotateY(180deg);}
      100% {-webkit-transform: rotateY(360deg);transform: rotateY(360deg);}

}

@keyframes rotate {
      0% {-webkit-transform: rotateY(180deg);transform: rotateY(180deg);}
      100% {-webkit-transform: rotateY(360deg);transform: rotateY(360deg);}

} 
  
button{
  
  cursor: pointer;
  background-color: rgba(255, 255, 255, 0.2);
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
  border-radius: 1rem;
  color: #101010;
  font-weight: bold;
  font-size: 16px;
  box-shadow:0 0 3px 0 grey ;
  border: none;
  padding: 9px 12px;
   
}

li{
  list-style: none;
}
.board{
  background-color: rgba(255, 255, 255, 0.2);
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
  border-radius: 1rem;
  width: 250px;
  height: 250px;
  margin: 0 auto;
  display: grid;
  grid-template: repeat(3,1fr)/ repeat(3,1fr);
}
.win{
  width:200px;
  margin:18px auto;
}


